.other-crypto {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 100px 0 0 0;
  text-align: center;
}

.box-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 100px 0 0 0;
  text-align: center;
}

.box {
  display: flex;
  width: 30%;
  box-shadow: 0 0px 20px -2px #949494;
  background-color: #f9f9f9;
  margin-right: 25px;
  border-radius: 20px;
}

.box-enterbutton {
  display: flex;
  width: 50%;
  box-shadow: 0 0px 20px -2px #949494;
  background-color: #f9f9f9;
  margin-right: 25px;
  border-radius: 20px;
  height: 300px;
}

.logo {
  height: 5%;
  width: 20%;
  padding: 20px;
  margin-left: 25px;
}

.logo-entc {
  height: 60%;
  width: 30%;
  margin-right: 20px;
  margin-left: 60px;
  margin-top: 50px;
}

.text {
  text-align: center;
  width: 60%;
}

.text-entc {
  text-align: center;
  width: 50%;
  margin-top: 50px;
  margin-left: 20px;
  font-size: 150%;
}
